import React from 'react';

const Highlight = () => {
    return (
        <>
            <div className="highlight">
                <div className="article-list raiting">
                    <img style={{marginBottom: "5px"}}src="/static/up.svg"/>
                    <div className="vote">78</div>
                    <img src="/static/down.svg"/>
                </div>
                <div className="article-list content">
                    <div className="article__title">
                        <a href="#">
                            <span>유튜브 테스트</span>
                        </a>
                    </div>
                    <div className="article-meta">
                        <span>1시간 전</span>
                    </div>
                </div>
                <div className="article-list thumbnail">
                    <img src="http://img.youtube.com/vi/BAgLhfmuV3Q/0.jpg"/>
                </div>
            </div>
            <style jsx>
                {`
                    .highlight {
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        height: 200px;
                        border-radius: 10px;
                    }
                    .highlight .raiting img {
                        width: 20px;
                        hight: 20px;
                    }
                    .article__title a span{
                        color: #2955BC;
                        font-size: 26px;
                        font-family: dotum;
                        text-decoration: none;
                    }
                    .thumbnail img{
                        width: 200px;    
                    }
                `}
            </style>
        </>
    )
};

export default Highlight;